import { CircularProgress } from "@mui/material";
import { WhatsappButton } from "components";
import ChangeLanguageButton from "components/change-language-button";
import { GalacticWallpaper, GoToFeedButtonWrapper, NotActivePlan } from "hoc";
import { UserLicenseAssignment } from "models";
import { useEffect, useState } from "react";
import { planService } from "services";
import { Header } from "./header";
import { useUserPlan } from "./hooks";
import Styles from "./index.module.css";
import { Table } from "./table";

export const UserPlanManagement = () => {
  const [users, setUsers] = useState<Array<UserLicenseAssignment> | undefined>();
  const userPlan = useUserPlan();
  const loadUsers = async () => {
    const response = await planService.findUserLicenseAssignments();
    setUsers(response.data);
  };

  useEffect(() => {
    loadUsers();
  }, []);

  let content = (
    <>
      <Header userPlan={userPlan} users={users} loadUsers={loadUsers} />
      <Table assignment={users} loadUsers={loadUsers} />
    </>
  );
  
  if (!users) {
    content = <CircularProgress />;
  }

  if(userPlan && !userPlan.id) {
    return <GoToFeedButtonWrapper><NotActivePlan /></GoToFeedButtonWrapper>;
  }

  return (
    <GalacticWallpaper isContentHorizontallyCentered={!users} isContentVerticallyCentered={!users}>
      <GoToFeedButtonWrapper>
        <section className={Styles.container}>{content}</section>
        <ChangeLanguageButton />
        <WhatsappButton position='left' />
      </GoToFeedButtonWrapper>
    </GalacticWallpaper>
  );
};
